import doneIcon from '../../images/done.jpg';
import orderDetailsStyles from './orderDetails.module.css';
import {getOrderNumber} from '../../services/selectors/orderDetails';
import {useAppSelector} from "../../utils/hooks/useAppDispatch";

function OrderDetails() {
  const orderNumber = useAppSelector(getOrderNumber);

  return (
    <div className={orderDetailsStyles.main} data-test-id='order_details'>
      <span className={orderDetailsStyles.orderNumber}>{orderNumber}</span>
      <h3 className={orderDetailsStyles.header}> </h3>
      <img className={orderDetailsStyles.image} src={doneIcon} alt=" "/> 
      <p className='text text_type_main-default mb-2'>   </p>
      <p className='text text_type_main-default text_color_inactive'>
            
      </p>
    </div>
  );
}

export default OrderDetails;
