import PropTypes from 'prop-types';

export const IngredientType = {
  _id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  proteins: PropTypes.number.isRequired,
  fat: PropTypes.number.isRequired,
  carbohydrates: PropTypes.number.isRequired,
  calories: PropTypes.number.isRequired,
  price: PropTypes.number.isRequired,
  image: PropTypes.string.isRequired,
  image_mobile: PropTypes.string.isRequired,
  image_large: PropTypes.string.isRequired,
  __v: PropTypes.number.isRequired,
}

export interface IIngredient {
  _id: string;
  name: string;
  type: string;
  proteins: number;
  fat: number;
  carbohydrates: number;
  calories: number;
  price: number;
  image: string;
  image_mobile: string;
  image_large: string;
  __v: number;
}

export interface IIngredientKey extends IIngredient {
  key: string;
}

export interface IOrder {
  order: {number: number}
}

export interface IError {
  success: false;
  message: string;
}

export interface IUserLogin {
  [key: string]: string | undefined;
  email: string;
  name: string;
  password: string | undefined;
}

export interface IOrderFeed {
  _id: string;   
  ingredients: string[];
  status: string;
  name: string;
  createdAt: string;
  updatedAt: string;
  number: number;
}

export enum WebsocketStatus {
		CONNECTING = 'CONNECTING',
		ONLINE = 'ONLINE',
		OFFLINE= 'OFFLINE'
}

export interface IWsMessage {
		success: boolean,
		orders: IOrderFeed[],
		total: number,
		totalToday: number
}

export type ICurrentIngredientsSlice = {
  bun: null | IIngredientKey;
  ingredients: IIngredientKey[];
};

export interface ICurrentIngredientSlice {
	ingredient: IIngredient | null
}

export interface IIngredientsSlice{
  ingredients: IIngredient[];
  requested: boolean;
  succeed: boolean;
  failed: boolean;
}

export interface IPasswordSlice {
  restorePasswordRequested: boolean,
  restorePasswordSucceed: boolean,
  restorePasswordFailed: boolean,
  resetPasswordRequested: boolean,
  resetPasswordSucceed: boolean,
  resetPasswordFailed: boolean
}

export interface IUserSlice {
  user: IUserLogin | null;
  isAuthChecked: boolean;
  isLoading: boolean;
  error: IError | null;
}

export interface IOrdersStore {
  orders: IOrderFeed[];
  total: number;
  totalToday: number;
  order?: IOrderFeed;
  status: WebsocketStatus;
  connectionError: string;
}

